$(document).ready(function () {
    var openMobileMenu = function () {
        $('.burger-top').click(function () {
            $(this).toggleClass('burger-top--active');
            $('.nav-top').toggleClass('nav-top--mobile');
        });
    };

    var openMobileSidebar = function () { 
        $('.sidebar__arrow').click(function () { 
            if ($('.icon-arrow').hasClass('close')){
                $('.icon-arrow').removeClass('close');
                $('.sidebar').removeClass('mobile');
            } else {
                $('.icon-arrow').addClass('close');
                $('.sidebar').addClass('mobile');
            }
         })
     }   

    openMobileMenu();
    openMobileSidebar();
});
